@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
*{box-sizing:border-box}body,button,input,select{font-family:'Poppins', sans-serif}body{padding:0;margin:0;font-family:'Poppins', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#FAFAFA;background-color:#212121}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}select,input{color:white;background-color:#263238;padding:6px 12px;font-size:16px;border:none;border-radius:3px}a{color:#03A9F4}button{background-color:#455A64;color:#FAFAFA;border:none;padding:6px 12px;font-size:16px;text-transform:uppercase;cursor:pointer;border-radius:3px;transition:all .15s ease}button:hover{background-color:#00acd7;box-shadow:0px 5px 10px 0px rgba(0,0,0,0.25)}button:disabled{opacity:0.3;box-shadow:none;cursor:not-allowed}

.container{min-height:100vh;max-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-flow:column;flex-flow:column}.code-editor>div>div{padding-top:20px}.share-input{width:100%}

.header{width:100%;background-color:#001419;box-shadow:0 0 30px 0 rgba(0,0,0,0.2);padding:10px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;z-index:10}.header>*{margin-right:10px}.header>*:last-child{margin-right:0}.header .logo{margin:0 25px 0 10px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;position:relative}.header .logo:hover .logo-info-container{pointer-events:all;opacity:1;-webkit-transform:scale(1);transform:scale(1);-webkit-filter:blur(0px);filter:blur(0px)}.header .logo .logo-info-container{transition:all .2s ease;position:absolute;top:10px;pointer-events:none;opacity:0;-webkit-transform:scale(1.02);transform:scale(1.02);-webkit-filter:blur(5px);filter:blur(5px)}.header .logo .logo-info-container .spacer{height:50px;background-color:transparent}.header .execute{background-color:#00acd7}.header .execute:hover{background-color:#0081d7}.header .share{margin-left:auto}

.lds-ellipsis{display:inline-block;position:relative;width:80px;height:10px}.lds-ellipsis div{position:absolute;width:11px;height:11px;border-radius:50%;background:#fff;-webkit-animation-timing-function:cubic-bezier(0, 1, 1, 0);animation-timing-function:cubic-bezier(0, 1, 1, 0)}.lds-ellipsis div:nth-child(1){left:8px;-webkit-animation:lds-ellipsis1 0.6s infinite;animation:lds-ellipsis1 0.6s infinite}.lds-ellipsis div:nth-child(2){left:8px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(3){left:32px;-webkit-animation:lds-ellipsis2 0.6s infinite;animation:lds-ellipsis2 0.6s infinite}.lds-ellipsis div:nth-child(4){left:56px;-webkit-animation:lds-ellipsis3 0.6s infinite;animation:lds-ellipsis3 0.6s infinite}@-webkit-keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes lds-ellipsis1{0%{-webkit-transform:scale(0);transform:scale(0)}100%{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@keyframes lds-ellipsis3{0%{-webkit-transform:scale(1);transform:scale(1)}100%{-webkit-transform:scale(0);transform:scale(0)}}@-webkit-keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}@keyframes lds-ellipsis2{0%{-webkit-transform:translate(0, 0);transform:translate(0, 0)}100%{-webkit-transform:translate(24px, 0);transform:translate(24px, 0)}}

.info-container{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;background-color:#001419;padding:20px 15px;border-radius:10px;box-shadow:0px 10px 20px 0 rgba(0,0,0,0.35)}.info-container p{margin:0}.info-container .mt{margin-top:20px}.info-container .table{white-space:nowrap;font-size:13px}.info-container .table tr>td:first-child{padding-right:20px;opacity:0.6;font-size:0.8em;text-transform:uppercase}

.result-viewer{font-family:'Source Code Pro', monospace;z-index:10;width:100%;max-height:50vh;overflow-y:auto;bottom:0;position:absolute;box-shadow:0 0 30px 0 rgba(0,0,0,0.2)}.result-viewer>div{padding:10px;background-color:#263238}.result-viewer .heading{font-size:0.8em;color:#90A4AE;margin-bottom:5px}.result-viewer .stderr{color:#f44336}

#snackbar-outer-container{position:fixed;display:-webkit-flex;display:flex;bottom:7vh;z-index:20;width:100%;transition:all 0.25s ease;height:0px;-webkit-transform:translateY(50px);transform:translateY(50px);opacity:0}#snackbar-outer-container.show{-webkit-transform:translateY(0px);transform:translateY(0px);opacity:1;height:100vh}.snackbar-container{margin:auto auto 0 auto;padding:15px 30px;border-radius:5px;pointer-events:all;box-shadow:0px 10px 20px 0px rgba(0,0,0,0.35);height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}

