.info-container {
    width: fit-content;
    height: fit-content;
    background-color: #001419;
    padding: 20px 15px;
    border-radius: 10px;
    box-shadow: 0px 10px 20px 0 rgba(0, 0, 0, 0.35);

    p {
        margin: 0;
    }

    .mt {
        margin-top: 20px;
    }

    .table {
        white-space: nowrap;
        font-size: 13px;

        tr > td:first-child {
            padding-right: 20px;
            opacity: 0.6;
            font-size: 0.8em;
            text-transform: uppercase;
        }
    }
}